@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Montserrat", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  font-weight: 300;
}

h1,
h2,
h3,
h4,
h5,
h6,
button,
a {
  font-weight: 400;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #000000;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(68, 76, 122);
  border-radius: 20px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(146, 136, 230);
}

:root {
  --primary: rgb(73, 21, 69);

  --textLight: #1be5fe;

  --fontVeryBig: 58px;
  --fontBig: 48px;
  --fontNormal: 32px;
  --fontSmall: 20px;

  --spaceNormal: 50px;
}

@media screen and (max-width: 668px) {
  :root {
    --fontVeryBig: 34px;
    --fontBig: 28px;
    --fontNormal: 22px;
    --fontSmall: 16px;

    --spaceNormal: 30px;
  }
}

.App {
  text-align: center;
  min-height: 100vh;
  background: linear-gradient(359deg, #222146, #272128);
  color: white;
  overflow: hidden;
}

ul li {
  list-style: none;
  cursor: pointer;
}

